import React, { useContext, useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import cernerImage from "../Assets/cerner.png";
import sailbotTeam from "../Assets/sailbot-team.jpg";
import sailbotBoat from "../Assets/sailbot-boat.jpg";

const Experience = () => {
  return (
    <div>
      <Container fluid className="home-section" id="home">
      <Container className="home-content">
        <Row lg={3}>
          <Col md={7}>
            <h1>Cerner</h1>
            <p>
              During my time at Cerner I have learned many skills that will help me through the rest of my career. 
            </p>
          </Col>
          <Col md={7}>
            <img
              src={cernerImage}
              alt="about pic"
              className="img-fluid"
              width="600"
              height="500"
            />
          </Col>
        </Row>
        <Row>
            <Col>
            <h1>Sailbot</h1>
            During my time at Iowa State University, I was the Electonics Team Lead on Sailbot (The Autonomous Sailboat Project). 
            The goal of this team was to create a fully autonomous sailboat that could complete various tasks during competition. 
            Besides learning a wealth of technical skills, I learned how work with a team made up of many other engineering disciplines.
            </Col>
          <Col md={7}>
            <img
              src={sailbotTeam}
              alt="about pic"
              className="img-fluid"
              width="600"
              height="500"
            />
          </Col>
          <Col md={7}>
            <img
              src={sailbotBoat}
              alt="about pic"
              className="img-fluid"
              width="600"
              height="500"
            />
          </Col>
        </Row>
      </Container>
      </Container>
    </div>
  );
};

export default Experience;
